.search-input-parent{
    width: 100%;
    margin: auto;
    text-align: center;
}

.search-input{
    margin: 50px;
    width: 250px;
    height: 50px;
    font-size: 100%;
    font-family: 'Courier New', Courier, monospace;
    padding-left: 15px;
    border-radius: 50px;
    border: 1px solid #aaa;
}

.search-input:focus{
    outline: none;
}